<template>
  <div>
      <el-tabs type="card" v-model="active" @tab-click="clickTab">
          <el-tab-pane label="基础指标类型" name="BasicQuotaType">
              <router-view></router-view>
          </el-tab-pane>
          <el-tab-pane label="基础指标" name="BasicQuota">
              <router-view></router-view>
          </el-tab-pane>
          <el-tab-pane label="衍生指标" name="DeriveQuota">
              <router-view></router-view>
          </el-tab-pane>
          <el-tab-pane label="指标配置管理" name="QuotaSettings">
              <router-view></router-view>
          </el-tab-pane>
      </el-tabs>
  </div>
</template>

<script>
  export default {
    name: 'Quota',
    mixins: [],
    components: {},
    data() {
      return {
          active: 'BasicQuotaType'
      }
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
          vm.active = to.name;
      });
    },
    created() {},
    mounted() {},
    computed: {},
    methods: {
        clickTab(tab) {
            this.$router.replace({ name: tab.name });
        }
    },
    filters: {},
    watch: {},
    destroyed() {},
    beforeRouteLeave(to, from, next) {
      next();
    }
  }
</script>

<style lang="scss" scoped>

</style>



